<template>
  <div>
    <div class="page-title">
      <h3> </h3>
    </div>

    <div class="history-chart">
      <canvas ref="canvas"></canvas>
    </div>

    <Loader v-if="loading"/>
    <p class="center" v-else-if="!records.length">  . <router-link to="/record"> .</router-link></p>
    <section v-else>
      <history-table :records="items"/>
      <Paginate
        v-model="page"
        :page-count="pageCount"
        :click-handler="pageChange"
        :prev-text="''"
        :next-text="''"
        :container-class="'pagination'"
        :page-class="'waves-effect'"
      />
    </section>
  </div>
</template>

<script>
  import HistoryTable from '@/components/history/HistoryTable'
  import paginationMixin from '@/mixins/pagination.mixin'
  import {Pie} from 'vue-chartjs'

  export default {
    name: 'History',
    components: {
      HistoryTable
    },
    mixins: [paginationMixin, Pie],
    metaInfo() {
      return {title: this.$title('Menu_History')}
    },
    data: () => ({
      loading: true,
      records: [],
    }),
    async mounted () {
      this.records = await this.$store.dispatch('fetchRecords')
      const categories = await this.$store.dispatch('fetchCategories')

      this.setup(categories)

      this.loading = false
    },
    methods: {
      setup(categories) {
        this.setupPagination(this.records.map(record => {
          return {
            ...record,
            categoryName: categories.find(c => c.id === record.catId).title,
            typeClass: record.type === 'income' ? 'green' : 'red',
            typeText: record.type === 'income' ? '' : '',
          }
        }))

        this.renderChart({
          labels: categories.map(c => c.title),
          datasets: [{
            label: '  ',
            data: categories.map(c => {
              return this.records.reduce((total, r) => {
                if (r.catId === c.id && r.type === 'outcome') {
                  total += +r.amount
                }
                return total
              }, 0)
            }),
            backgroundColor: [
              'rgba(255, 99, 132, 0.2)',
              'rgba(54, 162, 235, 0.2)',
              'rgba(255, 206, 86, 0.2)',
              'rgba(75, 192, 192, 0.2)',
              'rgba(153, 102, 255, 0.2)',
              'rgba(255, 159, 64, 0.2)'
            ],
            borderColor: [
              'rgba(255, 99, 132, 1)',
              'rgba(54, 162, 235, 1)',
              'rgba(255, 206, 86, 1)',
              'rgba(75, 192, 192, 1)',
              'rgba(153, 102, 255, 1)',
              'rgba(255, 159, 64, 1)'
            ],
            borderWidth: 1
          }]
        })
      }
    }
  }
</script>

